.container {
    display: flex;
    height: 100vh;
    width: 100vw;
    overflow: hidden; /* Prevent scrolling */
    background: linear-gradient(to right, #f0f4f8, #e9ecef);
  }
  
  .apology-section {
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding: 20px;
    position: relative;
  }
  
  .button-container {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    position: relative;
  }
  
  .gallery-section {
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    padding-left: 10px;
    position: relative;
  }
  
  .gallery-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
  }
  
  .button {
    padding: 10px 20px;
    margin: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: #dc3545;
    color: white;
    font-weight: bold;
    transition: transform 0.2s;
  }
  
  .apology-text {
    margin: 20px 0;
    font-size: 24px;
  }
  
  .person-image {
    margin-top: 20px;
    font-size: 100px;
  }
  
  .yes-button {
    position: static; /* Yes button remains stationary */
  }
  
  .no-button {
    position: absolute; /* Allows free movement across the screen */
    left: 80px; /* Initial left position */
    top: 20px; /* Initial top position */
  }
  
  /* Responsive styles for iPhone 12 */
  @media (max-width: 390px) {
    .apology-section {
      width: 100%;
      height: 60%; /* Adjust height */
      padding: 10px;
    }
  
    .gallery-section {
      width: 100%;
      height: 40%; /* Adjust height */
      padding-left: 0;
    }
  
    .button {
      padding: 8px 16px; /* Smaller buttons */
      font-size: 14px; /* Adjust font size */
    }
  
    .apology-text {
      font-size: 20px; /* Adjust text size */
    }
  
    .person-image {
      font-size: 80px; /* Adjust emoji size */
    }
  }
  